   }
#notepad fieldset {
    margin: 2px auto 0px;
    width: 600px;
    height: 36px;
    background-color: #f2f2f2;
    border: none;
    padding: 2px;
}
#notepad button {
    width: 5ex;
    text-align: center;
    padding: 1px 3px;
    height:30px;
    width:30px;
    background-repeat: no-repeat;
    background-size: cover;
    border:none;
}
